@import url(https://fonts.googleapis.com/css2?family=Fondamento&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
}
.map{height:700px;width:700px;margin:0 auto;background-position:center;background-size:contain;background-repeat:no-repeat}.map__title{text-align:center}@media (max-width: 1050px){.map{height:500px;width:500px}}@media (max-width: 800px){.map{max-width:100%}}

.map-container{overflow:hidden;text-align:center}.map-container__title{font-size:2rem;font-weight:700}

.target-item{text-align:left;border:1px solid #000000;border-top:none;cursor:pointer;padding:5px}.target-item:hover{background-color:lightblue}.target-item__container{margin:10px;width:47%}.target-item__title{cursor:pointer;-webkit-user-select:none;padding:5px;margin:0;border:2px solid blue}.target-item__title:hover{background-color:lightpink}.target-item__name{font-weight:700}.target-item p{margin:5px;font-size:.9em}@media (max-width: 1400px){.target-item__container{width:100%}}

.target-container{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;color:black}

.rank-item{margin-bottom:10px}.rank-item__title{display:block;border:2px solid green;cursor:pointer;-webkit-user-select:none;padding:5px;font-size:1.25rem;font-weight:700}.is-nav-open{-webkit-transform:rotate(90deg);transform:rotate(90deg)}

.rank-container{padding:0px 10px 10px 10px;text-align:center;max-height:85vh;overflow-y:auto;font-family:sans-serif}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:transparent}::-webkit-scrollbar-thumb{background:#9F2637}::-webkit-scrollbar-thumb:hover{background:#761c29}

.dropdown-header{text-align:center;-webkit-user-select:none}.dropdown-header__title{display:inline-block;text-transform:uppercase;color:#9F2637;cursor:pointer;width:500px;border-bottom:2px solid #9F2637}.dropdown-header__title:hover{color:#c83045}

.dropdown-item{font-size:1.75em;padding:10px;width:500px;background-color:#191919;border-left:5px solid black;border-right:5px solid black;display:flex;justify-content:center}.dropdown-item__icon{height:40px}.dropdown-item__name{text-transform:uppercase;color:#9F2637;cursor:pointer}.dropdown-item__name:hover{color:#c83045}

.dropdown-list{display:flex;flex-direction:column;justify-content:center;align-items:center}.dropdown-list__container{position:absolute;margin-left:auto;margin-right:auto;left:0;right:0;text-align:center;z-index:1}

.homepage__container{margin:0 auto;padding-top:20px;font-family:'Fondamento', sans-serif;color:#9F2637;text-transform:uppercase;position:absolute;left:0;right:0;bottom:0;top:0}.homepage__dropdown-section{display:flex;justify-content:center}.homepage__dropdown-section-text{display:inline-block}.homepage__logo{width:400px;-webkit-animation:loading 60s linear infinite;animation:loading 60s linear infinite;-webkit-animation-delay:5s;animation-delay:5s}@-webkit-keyframes loading{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}5%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes loading{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}5%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.homepage__row{display:flex;justify-content:center}.homepage__map-section{width:700px}.homepage__no-selection{display:flex;justify-content:center;align-items:center;height:80vh}.homepage__rank-section{width:900px}@media (max-width: 800px){.homepage__row{flex-direction:column;align-items:center}.homepage__map-section{width:100%;height:auto}.homepage__rank-section{width:100%}}

